.movie-card{
    text-decoration: none;
    min-width: 300px;
    max-width: 300px;
    margin-bottom: 50px;
    border-radius: 15px 15px 0 0;
    overflow: hidden;
    background: white;
    box-shadow: 5px 5px 8px black;
    position: relative;
    .img-holder{
        width: 100%;
        overflow: hidden;
        img{
            width: 100%;
            height: 400px;
            object-fit: cover;
            transition: all ease 0.5s;
        }
    }
    &:hover{
        .img-holder{
            img{
                transform: scale(1.3);
            }
        }
    }
    .rating{
        position: absolute;
        top: 10px;
        right: 0;
        padding: 10px 20px 10px 10px;
        border-radius: 15px 0 0 15px;
        color: white;
        font-weight: bold;
        background: red;
    }
    .title-holder{
        color: black;
        padding: 5px ;
        .title{
            margin-bottom: 5px;
            font-size: 20px;
        }
        .release-date{
            font-size: 15px;
            color: red;
            font-weight: bold;
        }
    }
}   