.actor-card{
    width: 200px;
    margin-bottom: 30px;
    position: relative;
    img{
        width: 100%;
        height: 300px;
        object-fit: cover;
    }
    .actor-name{
        bottom: 0;
        left: 0;
        position: absolute;
        padding: 5px;
        background: red;
        color: white;
    }
}