.wrapper{
    display: flex;
    flex-wrap: wrap;
    margin-top: 50px;
    .left{
        width: 50%;
        img{
            width: 100%;
        }
    }
    .right{
        width: 50%;
        padding: 0 30px;
        span{
            color: red;
            font-weight: bold;
            margin: 20px 0;
            display: inline-block;
        }
        p{
            color: white;
            font-weight: lighter;
        }
    }
}